  <div id="app">
    <SquareLocations :visible="visible" @closeModal="closeModal"/>
    <button
      class="btn-black"
      @click="visible = !visible"
    >
      Press me!
    </button>
  </div>
</template>

<script>
import SquareLocations from './components/SquareLocations.vue'

export default {
  name: 'App',
  components: {
    SquareLocations
  },
  data () {
    return {
      visible: false
    }
  },
  methods: {
    closeModal () {
      this.visible = false
    }
  }
}
</script>

